import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import * as Rx from 'rxjs';
import { environment } from 'src/environments/environment';
import { Location } from '@angular/common';
import Swal from 'sweetalert2/dist/sweetalert2.js';

@Component({
  selector: 'app-add-case',
  templateUrl: './add-case.component.html',
  styleUrls: ['./add-case.component.css']
})
export class AddCaseComponent implements OnInit {
  model: any;
  caseForm: FormGroup
  selectedFile: any;
  caseNo: any = "";
  btnLabel = "Submit"
  userName: any;
  userLoggedIN: any;
  isSuperAdmin: boolean;
  notification_id: any;
  constructor(
    private router: Router,
    private fb: FormBuilder,
    private toastr: ToastrService,
    private http: HttpClient,
    private activateRoute: ActivatedRoute,
    private location: Location
  ) { 
    this.activateRoute.params.subscribe(caseno => {
      this.caseNo = caseno.caseNo || '';
    })
     this.userLoggedIN = JSON.parse(localStorage.getItem('user'));
    this.userName = this.userLoggedIN && this.userLoggedIN.userName;
    this.notification_id = this.userLoggedIN && this.userLoggedIN.notification_ids || '';

   
  }

  getCase() {
    this.http.get(`https://svu-client.herokuapp.com/api/svu/v1/getcasebyid?caseNo=${this.caseNo}`).subscribe(res => {
      let caseDate = res['data'].date;
      let caseData = res && res['data'] ;
      let dateObj = {
        year: new Date(caseDate).getFullYear(),
        month: new Date(caseDate).getMonth(),
        day: new Date(caseDate).getDate()
      }
        this.caseForm.get('date').patchValue(dateObj)
        this.caseForm.get('caseNo').patchValue(caseData && caseData.caseNo)
        this.caseForm.get('underSection').patchValue(caseData && caseData.underSection)
        this.caseForm.get('nameOfAccuse').patchValue(caseData && caseData.nameOfAccuse)
        this.caseForm.get('createdBy').patchValue(caseData && caseData.createdBy)
        // this.caseForm.get('InvStatus').patchValue(caseData && caseData.InvStatus)
        this.caseForm.get('prosSanctionStatus').patchValue(caseData && caseData.prosSanctionStatus)
        this.caseForm.get('trialStatus').patchValue(caseData && caseData.trialStatus)
        this.caseForm.get('chartSheetDate').patchValue(caseData && caseData.chartSheetDate)
        this.caseForm.get('depProceeding').patchValue(caseData && caseData.depProceeding)
      if(caseData && caseData.notification_id) {
        this.caseForm.get('notification_id').patchValue(caseData && caseData.notification_id)
      }

        this.caseForm.get('depProceeding').patchValue(caseData && caseData.depProceeding)
        this.caseForm.get('reviewComment').patchValue(caseData && caseData.reviewComment);
        this.caseForm.get('confiscationStatus').patchValue(caseData && caseData.confiscationStatus);
        this.caseForm.get('invOfficer').patchValue(caseData && caseData.invOfficer)
        if(!this.isSuperAdmin) {
          this.caseForm.get('reviewComment').disable();
        }
        this.btnLabel = "Update"
        if(this.caseNo !== '') {
          if(this.userLoggedIN && this.userLoggedIN.role)
          if(this.userLoggedIN && this.userLoggedIN.role === 'super-admin' || this.userLoggedIN && this.userLoggedIN.role === 'admin') { 
            if(this.userName !== this.caseForm.get('createdBy').value) {
              this.caseForm.get('InvStatus').setValidators([])
              this.isSuperAdmin = true;
              this.caseForm.get('InvStatus').updateValueAndValidity();
              this.caseForm.disable()
              this.caseForm.get('reviewComment').enable()
            }
          }
        }
       
      })   
  }

  ngOnInit(): void {
    this.caseForm = this.fb.group({
      file: [''],
      date: ['', [Validators.required]],
      caseNo: ['', [Validators.required]],
      underSection: ['', [Validators.required]],
      nameOfAccuse: ['', [Validators.required]],
      InvStatus: ['', [Validators.required]],
      prosSanctionStatus: ['', [Validators.required]],
      trialStatus: ['', [Validators.required]],
      confiscationStatus: ['', [Validators.required]],
      chartSheetDate: ['', [Validators.required]],
      depProceeding: ['', [Validators.required]],
      invOfficer: ['', [Validators.required]],
      status: ['pending', [Validators.required]],
      createdBy: [''],
      reviewComment: [''],
      notification_id: [this.notification_id]
      

    })
    if(this.caseNo !== '') {
      this.getCase()
    }
    
    if(this.userLoggedIN && this.userLoggedIN.role === 'general') { 
      this.caseForm.get('reviewComment').disable()
    }
  
  }

  submit() {
    Swal.fire({
      title: 'Are you sure?',
      text: 'You Want To Submit Case',
      icon: 'warning',
      showCancelButton: true,
      cancelButtonColor: 'red',
      confirmButtonText: 'Yes',
      cancelButtonText: 'No'
    }).then((result) => {
      if (result.value) {
        if(this.isSuperAdmin) {
          // addcomment
          if(this.caseNo  !== '') {
            const commentCache = this.caseForm.get('reviewComment').value;
            const id = this.caseForm.get('notification_id').value;
            const createdBy = this.caseForm.get('createdBy').value;
            const toSendComment = `${commentCache} (Review by ${this.userLoggedIN && this.userLoggedIN.name})`
            this.http.get(`https://svu-client.herokuapp.com/api/svu/v1/addcomment?caseNo=${this.caseNo}&reviewComment=${toSendComment}&id=${id}&created_by=${createdBy}`).subscribe(res => {
            
            Swal.fire(
                'Case Added!',
                'Case Has Been Successfully Added',
                'success'
              ) 
            this.router.navigate(['tables'])
             
            })
          } else {
            let getDate = this.caseForm.get('date').value
            let month = getDate.month;
            let year = getDate.year;
            let day = getDate.day
            const isoDate = month.toString() + "/" + day.toString()  + "/" + year.toString();
            this.caseForm.get('date').setValue(new Date(isoDate).toISOString())
            this.caseForm.get('createdBy').setValue(this.userName)
            // this.caseService.addCase(this.caseForm.getRawValue()).subscribe(res => {
            //   this.router.navigate(['tables'])
            // })
            if(this.caseNo !== "") {
              
              return this.http.put(`https://svu-client.herokuapp.com/api/svu/v1/updateCase?caseNo=${this.caseNo}`, this.caseForm.getRawValue()).subscribe(res => {
                Swal.fire(
                  'Case Updated!',
                  'Case Has Been Successfully Updated',
                  'success'
                )
              this.router.navigate(['tables'])
                
              })
            } else {
              return this.http.post('https://svu-client.herokuapp.com/api/svu/v1/addcase', this.caseForm.getRawValue()).subscribe(res => {
                Swal.fire(
                  'Case Added!',
                  'Case Has Been Successfully Added',
                  'success'
                )  
              this.router.navigate(['tables'])
                
              })
            }
          
          }
        } else {
          let getDate = this.caseForm.get('date').value
          let month = getDate.month;
          let year = getDate.year;
          let day = getDate.day
          const isoDate = month.toString() + "/" + day.toString()  + "/" + year.toString();
          this.caseForm.get('date').setValue(new Date(isoDate).toISOString())
          this.caseForm.get('createdBy').setValue(this.userName)
          // this.caseService.addCase(this.caseForm.getRawValue()).subscribe(res => {
          //   this.router.navigate(['tables'])
          // })
          if(this.caseNo !== "") {
            
            return this.http.put(`https://svu-client.herokuapp.com/api/svu/v1/updateCase?caseNo=${this.caseNo}`, this.caseForm.getRawValue()).subscribe(res => {
              this.router.navigate(['tables'])
              Swal.fire(
                'Case Updated!',
                'Case Has Been Successfully Updated',
                'success'
              )
            })
          } else {
            return this.http.post('https://svu-client.herokuapp.com/api/svu/v1/addcase', this.caseForm.getRawValue()).subscribe(res => {
              this.router.navigate(['tables'])
              Swal.fire(
                'Case Added!',
                'Case Has Been Successfully Added',
                'success'
              )  
            })
          }
        }
      // For more information about handling dismissals please visit
      // https://sweetalert2.github.io/#handling-dismissals
      } 
    })
   
      
     

  }
  private _createFormDataV2(data) {
		const formData = new FormData();
		if (data && data instanceof Object) {
			for (const key in data) {
				formData.append(key, data[key]);
			}
		}
		return formData;
	}
 uploadToServerV4(file: File, types: string, size: number) {
		const contentType = file.type;
		const formData = this._createFormDataV2({
			file,
			types,
			size,
			contentType,
			path: 'Candidate'
    });
    console.log(formData);
    
		// const URL = `${environment.documentServiceUrl}/api/document/v3`;
		// const URL = `${environment.documentServiceUrl}/api/document/v1`;
  }
  
  studentBulkUpload(file): Rx.Observable<any> {
    const formData = new FormData();
    formData.append('file', file);
    return this.http.post(`https://svu-client.herokuapp.com/api/svu/v1/saveFiles?caseNo=${this.caseForm.get('caseNo').value}`, formData)
  }
  upload() {
  }

  onFileUpload(event) {
    if(this.caseForm.get('caseNo').value === '') {
      Swal.fire({
        icon: 'error',
        title: 'Alert',
        text: 'Please enter case number before uploading file!',
      })
      this.caseForm.get('file').patchValue('')
      this.caseForm.get('InvStatus').patchValue('')
    } else {
      this.caseForm.get('file').patchValue( event.target.files[0])
      this.studentBulkUpload(this.caseForm.get('file').value).subscribe(res => {
       console.log(res);
     })
    }
  
   
  }

  previewFile() {
    this.http.get(`https://svu-client.herokuapp.com/api/svu/v1/downloadFile?caseNo=${this.caseNo}`).subscribe(res => {
      const file = res && res['data'] && res['data']['fileName']
      window.open(file, 'blank')
      })
  }

  goBack() {
    this.location.back()
  }

}
