import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { Route } from '@angular/compiler/src/core';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';
import Swal from 'sweetalert2/dist/sweetalert2.js';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit, OnDestroy {
  userForm: FormGroup;
  constructor(
    private fb: FormBuilder,
    private http: HttpClient,
    private router: Router
  ) {}
  ngOnInit() {
    this.userForm = this.fb.group({
      userName: ['', [Validators.required]],
      password: ['', [Validators.required]]
    })
  }
  ngOnDestroy() {
  
  }

  login() {
    const userName = this.userForm.get('userName').value
    const password = this.userForm.get('password').value
    this.http.get(`https://svu-client.herokuapp.com/api/svu/v1/user/login?userName=${userName}&password=${password}`).subscribe(res => {
    if(res['data'] === 'user not found') {
    //  alert('User not found')
    Swal.fire({
      icon: 'error',
      title: 'Login Failed !!!',
      text: 'User Not Found',
      footer: '<p style="color:red;font-weight:bold;" href>Contact Super Admin</p>'
    })
    } 
    else  {
      localStorage.setItem('user', JSON.stringify(res && res['data']));
      this.router.navigate(['tables'])
      
    }
  })
   
  }


}
