import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import * as Rx from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute, Router } from '@angular/router';
import { environment } from 'src/environments/environment';
import { Location } from '@angular/common';
import Swal from 'sweetalert2/dist/sweetalert2.js';

@Component({
  selector: 'app-add-enquiry',
  templateUrl: './add-enquiry.component.html',
  styleUrls: ['./add-enquiry.component.css']
})
export class AddEnquiryComponent implements OnInit {
  enquiryForm: FormGroup
  enqNo: any = "";
  btnLabel: any = "";
  data: any;
  userName: any;
  user: any;
  userLoggedIN: any;
  isSuperAdmin: boolean;
  notification_id: any;
  constructor(
    private fb: FormBuilder,
    private http: HttpClient,
    private activateRoute: ActivatedRoute,
    private router: Router,
    private location: Location
  ) { 
    this.activateRoute.params.subscribe(enquiryNo => {
      this.enqNo = enquiryNo && enquiryNo.enqNo || ''
    })
    this.btnLabel = "Submit"
    if(this.enqNo !== '') {
      this.getEnquiry();
    }
    this.userLoggedIN = JSON.parse(localStorage.getItem('user'));
    this.notification_id = this.userLoggedIN && this.userLoggedIN.notification_ids || '';

    this.user = this.userLoggedIN && this.userLoggedIN.userName;
    console.log(this.user);
    
  }

  ngOnInit(): void {
    this.enquiryForm = this.fb.group({
      enquiryNo: ['', [Validators.required]],
      file: ['', [Validators.required]],
      date: ['', [Validators.required]],
      suspectName: ['', [Validators.required]],
      enqOfficerName: ['', [Validators.required]],
      status: ['pending'],
      createdBy: [this.user],
      reviewComment: [''],
      notification_id: [this.notification_id]
    })
    if(!this.isSuperAdmin) {
      this.enquiryForm.get('reviewComment').disable();
    }
  }

  getEnquiry() {

 
    this.http.get(`https://svu-client.herokuapp.com/api/svu/v1/getenqbyid?enqNo=${this.enqNo}`).subscribe(res => {
    this.data = res && res['data'];
    this.enquiryForm.get('enquiryNo').patchValue(this.data && this.data.enquiryNo)
    let caseDate =  res && res['data'] && res['data'].date;
    let dateObj = {
      year: new Date(caseDate).getFullYear(),
      month: new Date(caseDate).getMonth(),
      day: new Date(caseDate).getDate()
    }
    console.log(dateObj);
    
    this.enquiryForm.get('date').patchValue(dateObj)
    this.enquiryForm.get('createdBy').patchValue(this.data && this.data.createdBy)
    this.enquiryForm.get('reviewComment').patchValue(this.data && this.data.reviewComment)
    if(this.data && this.data.notification_id) {
      this.enquiryForm.get('notification_id').patchValue(this.data && this.data.notification_id)
    }
    this.enquiryForm.get('suspectName').patchValue(this.data && this.data.suspectName)
    this.enquiryForm.get('enqOfficerName').patchValue(this.data && this.data.enqOfficerName)
    this.enquiryForm.get('status').patchValue(this.data && this.data.status)
    
    if(this.enqNo !== '') {
      if(this.userLoggedIN && this.userLoggedIN.role)
      if(this.userLoggedIN && this.userLoggedIN.role === 'super-admin' || this.userLoggedIN && this.userLoggedIN.role === 'admin') { 
        if(this.user !== this.enquiryForm.get('createdBy').value) {
          this.enquiryForm.get('file').setValidators([])
          this.isSuperAdmin = true;
          this.enquiryForm.get('file').updateValueAndValidity();
          this.enquiryForm.disable()
          this.enquiryForm.get('reviewComment').enable()
        }
      }
    }
   
    })
  }
  onFileUpload(event) {
    this.enquiryForm.get('file').patchValue(event.target.files[0])
   this.studentBulkUpload(this.enquiryForm.get('file').value).subscribe(res => {
    console.log(res);
  })
  }

  studentBulkUpload(file): Rx.Observable<any> {
    const formData = new FormData();
    formData.append('file', file);
    return this.http.post(`https://svu-client.herokuapp.com/api/svu/v1/saveFiles?caseNo=${this.enquiryForm.get('enquiryNo').value}`, formData)
  }

  submit() {
    Swal.fire({
      title: 'Are you sure?',
      text: 'Are You sure, You Want To Dispose Case',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes',
      cancelButtonColor: 'red',
      cancelButtonText: 'No'
    }).then((result) => {
      if (result.value) {
        if(this.userLoggedIN && this.userLoggedIN.role === 'super-admin' || this.userLoggedIN && this.userLoggedIN.role === 'admin') {
          if(this.enqNo !== '') {
            const commentCache = this.enquiryForm.get('reviewComment').value;
            const toSendComment = `${commentCache} (Review by ${this.userLoggedIN && this.userLoggedIN.name})`
            const createdBy = this.enquiryForm.get('createdBy').value;
            this.http.get(`https://svu-client.herokuapp.com/api/svu/v1/addenqcomment?enqNo=${this.enqNo}&reviewComment=${toSendComment}&id=${this.enquiryForm.get('notification_id').value}&created_by=${createdBy}`).subscribe(res => {
              this.router.navigate(['tables'])
              Swal.fire(
                'Enquiry Updated!',
                'Enquiry Has Been Successfully Updaed',
                'success'
              )
            
            })
          } 
          
          
          else {
            let getDate = this.enquiryForm.get('date').value
            let month = getDate.month;
            let year = getDate.year;
            let day = getDate.day
            const isoDate = month.toString() + "/" + day.toString()  + "/" + year.toString();
            this.enquiryForm.get('date').setValue(new Date(isoDate).toISOString())
          const payLoad = {
            date: this.enquiryForm.get('date').value,
            enqOfficerName: this.enquiryForm.get('enqOfficerName').value,
            enquiryNo: this.enquiryForm.get('enquiryNo').value,
            status: this.enquiryForm.get('status').value,
            suspectName: this.enquiryForm.get('suspectName').value,
          }    
          if(this.enqNo === "") {
            return this.http.post('https://svu-client.herokuapp.com/api/svu/v1/addEnquiry', this.enquiryForm.getRawValue()).subscribe(res => {
              this.router.navigate(['tables'])
              Swal.fire(
                'Enquiry Added!',
                'Enquiry Has Been Successfully Added',
                'success'
              )
            
            })
          } else {
            return this.http.put(`https://svu-client.herokuapp.com/api/svu/v1/updateEnquiry?enqNo=${this.enqNo}`, this.enquiryForm.getRawValue()).subscribe(res => {
              this.router.navigate(['tables'])
              Swal.fire(
                'Enquiry Updated!',
                'Enquiry Has Been Successfully Updated',
                'success'
              )
            
            })
          }
         
          }
          
      } else {
        let getDate = this.enquiryForm.get('date').value
        let month = getDate.month;
        let year = getDate.year;
        let day = getDate.day
        const isoDate = month.toString() + "/" + day.toString()  + "/" + year.toString();
        this.enquiryForm.get('date').setValue(new Date(isoDate).toISOString())
      const payLoad = {
        date: this.enquiryForm.get('date').value,
        enqOfficerName: this.enquiryForm.get('enqOfficerName').value,
        enquiryNo: this.enquiryForm.get('enquiryNo').value,
        status: this.enquiryForm.get('status').value,
        suspectName: this.enquiryForm.get('suspectName').value,
      }    
      if(this.enqNo === "") {
        return this.http.post('https://svu-client.herokuapp.com/api/svu/v1/addEnquiry', this.enquiryForm.getRawValue()).subscribe(res => {
          this.router.navigate(['tables'])
        })
      } else {
        return this.http.put(`https://svu-client.herokuapp.com/api/svu/v1/updateEnquiry?enqNo=${this.enqNo}`, this.enquiryForm.getRawValue()).subscribe(res => {
          this.router.navigate(['tables'])
          
        })
      }
     
      }
    
    
     
      // For more information about handling dismissals please visit
      // https://sweetalert2.github.io/#handling-dismissals
      } 
    })

    
    


  }

  previewFile() {
    this.http.get(`https://svu-client.herokuapp.com/api/svu/v1/downloadFile?caseNo=${this.enqNo}`).subscribe(res => {
      const file = res && res['data'] && res['data']['fileName']
      window.open(file, 'blank')
      })

  }

  goBack() {
    this.location.back()
  }
}
